.btn_expand.cross {
  cursor: pointer;
  @include size(20px);
  z-index: 10;

  &:before,&:after {
    content: "";
    display: block;
    position: absolute;
    display: flex;
    width: 100%;
    top: 50%;
    left: 50%;
    height: 2px;
    background-color: $color_theme;
    @include trans; }
  &:before {
    transform: translate(-50%,-50%) rotate(0deg); }
  &:after {
    transform: translate(-50%,-50%) rotate(90deg); }
  &.active {
    &:before {
      transform: translate(-50%,-50%) rotate(180deg); }
    &:after {
      transform: translate(-50%,-50%) rotate(180deg); } } }



