//----------

.btn {
  border-radius: 2px;
  letter-spacing: 2px;
  padding: 10px grid_sz(0,1);
  font-weight: 300;
  font-size: 15px;
  letter-spacing: 4px;
  text-decoration: none !important;
  cursor: pointer;
  display: inline-block;
  vertical-align: middle;
  & > div {
    display: inline-block; }


  @include trans; }

.btn-primary {
  border: none;
  background-color: $color_theme;
  color: $color_white;
  &:hover {
    background-color: lighten($color_theme,15);
    color: $color_white; } }

.btn-primary-lighter {
  border: none;
  background-color: lighten($color_theme,7);
  color: $color_white;
  &:hover {
    background-color:  lighten($color_theme,15);
    color: $color_white;
    border-color:  lighten($color_theme,7); } }

.btn-transparent {
  background-color: transparent;
  border: solid 1px $color_white;
  color: $color_white;
  &:hover {
    background-color: $color_white;
    color: $color_theme;
    border-color: $color_white; } }

.btn-underline {
  position: relative;
  height: grid_sz(0,2);
  background-color: transparent;
  color: $color_brown;
  padding-left: grid_sz(0,1);
  padding-right: calc( #{grid_sz(0,1)} - 10px );
  // text-decoration: underline
  // text-underline-position: under
  line-height: 40px;
  font-size: 18px;
  @include flex_center;
  span {
    display: inline-block;
    // height: 40px
    height: grid_sz(0,2);
    box-sizing: border-box;
    letter-spacing: 5px;
    padding-top: 5px;
    border-bottom: solid 2px $color_brown; }

  @include trans;
  // +flex_center
  display: inline-block;
  vertical-align: middle;
  // padding-top: 10px
  &:before,&:after {
    display: inline-block; }
  &:after {
    content: ">";
    margin-left: 10px;
    font-size: 20px;
    // display: block
    // position: absolute
    // right: -40px
    transform: scaleX(0.7);
 }    // +trans
  &.white {
    color: $color_white;
    span {
      border-color: $color_white; }
    &:hover {
      color: $color_theme;
      background-color: $color_white; } }
  &.black {
    color: $color_grey_light;
    span {
      border-color: $color_grey_light; }
    &:hover {
      color: $color_white;
      background-color: $color_grey_light; } }
  &.brown_bg {
    color: $color_white;
    border-color: transparent;
    background-color: $color_brown;
    padding-left: grid_sz(0,0.8);
    padding-right: grid_sz(0,0.8);
    &:after {
      color: lighten($color_brown,10); }
    &:hover {

      background-color: darken($color_brown,10); } }



  &:hover {
    color: $color_white;
    border-color: transparent;
    background-color: $color_brown; }



  &.white_outline {
    background-color: transparent;
    border: solid 1px white;
    // padding-right: 40px
    span {
      border-color: transparent; }

    &:hover {
      background-color: white;
      span,&:after {
        color: #222;
        @include trans; }
 }      // padding-right: 40px
    // padding-left: grid_sz(0,0.8)
 } }    // padding-right: grid_sz(0,0.8)

.btn_full {

  font-size: 24px;
