.slide_bullet {
  list-style: none;
  position: fixed;
  right: 40px;
  top: 50%;
  transform: translate(-50%,0px);
  z-index: 10;
  li {
    display: block;
    @include size(9px);
    border-color: $color_grey_light;
    border-radius: 50%;
    margin-bottom: 16px;
    cursor: pointer;
    @include trans;
    position: relative;
    &:after {
      content: "";
      display: block;
      border-radius: 50%;
      @include ab_center;
      @include size(6px);
      background-color: darken($color_grey,9);
      @include trans; }

    &:hover {
      border-color: $color_theme;
      &:after {
        background-color: $color_theme;
        @include size(12px); } }
    &.active {
      background-color: $color_theme;
      &:after {
        background-color: $color_theme;
        @include size(12px); } } }
  @include range_width(0px,800px) {
     display: none; } }


