section.section_solution {
  // background: url("../img/homepage/Home7.jpg") 60% bottom
  background-size: cover;
  // min-height: 130vh
  position: relative;
  padding-top: grid_sz(0,2);
  padding-bottom: grid_sz(0,2);
  text-align: center;

  .container.flex {
    min-height: grid_sz(0,24);
    height: auto;
    padding: 0px grid_sz(0,1);
    justify-content: center; }
  .slogan {
    margin-top: grid_sz(0,1);
    margin-bottom: $gutter_width;
    color: $color_grey_light;
    font-weight: 400; }
  &:before {
    @include cover_opacity(rgba(white,0.6)); }

  h3.section_title {
    margin-top: grid_sz(0,2);
    margin-bottom: grid_sz(0,2);
    color: $color_grey_dark;
    font-size: 24px; }
  .solution_box_row {
    margin-top: $gutter_width*1;
    // margin-bottom: $gutter_width*3
    min-height: 500px; }

  .solution_box {
    text-align: center;
    width: grid_sz(4,3);
    cursor: pointer;
    @include trans;
    @include range_width(0px,800px) {
      margin-bottom: grid_sz(0,1);
      width: 100%; }

    &:nth-child(2n) {
      margin-left: $gutter_width;
      margin-right: $gutter_width; }

    & .box_inner {
      display: block;
      position: relative;
      @include box_shadow;
      @include trans;
      height: grid_sz(0,19);
      overflow: hidden;
      cursor: pointer;

      .box_info {
        margin-top: grid_sz(0,3); }
      .box_btn {
        margin-bottom: grid_sz(0,3); }

      .box_text {
        @include flex_split("td");
        @include ab_full;
        z-index: 10; }

      &:after,&:before {
        @include pseudo_ab_full; }

      &:after {
        background-color: rgba(0,0,0,0.4);
        // opacity: 0
        @include trans; }

      &:hover,&:focus {
        height: grid_sz(0,24);
        background-color: transparent;
        color: #333;

        .box_info {
          h4.solution_title {
            color: $color_grey_dark;
            &:after {
              border-bottom: solid 2px  $color_grey_dark; } } }
        .btn {
          opacity: 1; }

        &:after {
          opacity: 0; } }

      h4 {
        color: $color_white; }

      h4.solution_title {
        line-height: 2em;
        color: $color_white;
        @include trans;
        font-weight: 400;

        &:after {
          content: "";
          display: block;
          width: grid_sz(1,0);
          margin: auto;
          margin-top: grid_sz(0,2);
          @include trans;
          border-bottom: solid 2px $color_white; } } }




    .btn {
      margin-bottom: $gutter_width*2;
      @include trans;
      opacity: 0;
      cursor: pointer; }

    &:nth-child(1) .box_inner:before {
      background-image: url("../img/homepage/Home8-1.jpg");
      background-position: 60% bottom;
      background-size: 105% auto; }


    &:nth-child(2) .box_inner:before {
      background-image: url("../img/homepage/Home8-2.jpg");
      background-position: 60% bottom;
      background-size: 105% auto; }


    &:nth-child(3) .box_inner:before {
      background-image: url("../img/homepage/Home8-3.jpg");
      background-position: 60% bottom;
      background-size: 105% auto; } }


  .row_else {
    padding: $gutter_width*1;
    padding-bottom: $gutter_width*2; } }


