//問題清單
.question_list {
  position: relative;
  .icon {
    @include size(18px);
    // background-color: darken($color_white,15)
    border-radius: 2px;
    position: relative;
    display: inline-block;
    position: absolute;
    left: -26px;
    top: 5px;
    opacity: 0.7;
    cursor: pointer;
    @include trans;

    &:hover {
      background-color: $color_theme;
      &:before,&:after {
        background-color: $color_white; } }

    &:before,&:after {
      // transform-origin: center center
      @include trans;
      display: block;
      content: "";
      width: 10px;
      height: 2px;
      background-color: $color_theme;

      @include ab_center; }
    &.icon_plus {

      &:before {
        transform: translate(-50%,-50%) rotate(-90deg); }
      &:after {
        transform: translate(-50%,-50%) rotate(180deg); } } }

  & {
    list-style: none;
    padding-left: $gutter_width*1.5; }

  li {
    margin-bottom: $gutter_width;
    position: relative;

    &.open {
      .answer {
        max-height: 250px;
        margin-top: $gutter_width/2; } } }

  .question {
    color: $color_grey_light;
    font-size: 16px;
    font-weight: 400;
    letter-spacing: 1px;
    cursor: pointer; }
  .answer {
    color: $color_theme;
    font-weight: 400;
    margin-top: 0;
    max-height: 0px;
    overflow: hidden;
    @include trans;

    // max-height: 0px
 }    // overflow: hidden

  input[type=checkbox]:checked+.answer {
    .answer {
      display: block;
      max-height: 50px; } } }

//-------------------------
.fade-enter-active,.fade-leave-active {
  transition: opacity .5s; }

.fade-enter,.fade-leave-to {
  opacity: 0; }
.fade-enter-to,.fade-leave {
  opacity: 1; }

.fade-delay-enter-active {
  transition: opacity .5s .5s; }
.fade-delay-leave-active {
  transition: opacity .5s; }

.fade-delay-enter,.fade-delay-leave-to {
  opacity: 0; }
.fade-delay-enter-to,.fade-delay-leave {
  opacity: 1; }

