.page_about {

  .btn_expand.cross {
    position: absolute;
    right: 20px;
    top: 20px; }


  .section_hero {
    // padding-top: grid_sz(0,5)
    .container.full {
      position: absolute;
      top: 0;

      background-image: url("/img/about/1.jpg");
      background-size: cover;
      background-position: center top;
      .col_left {
        width: calc(50% + #{grid_sz(1,1.5)});

        &:before {
          @include pseudo_ab_full;
          background-color: rgba($color_grey_dark,0.1); } }

      .col_right {
        width: calc(50% - #{grid_sz(1,1.5)});
        background-color: rgba(#fff,0.7); }


      @include rwd_md {
        display: block;
        .col_left,.col_right {
          padding: grid_sz(0,1);
          width: 100%;
          display: none; } } }

    .container_content {
      .col_right {
        padding-left: grid_sz(1.5,1); }

      .timelog {
        position: relative;
        cursor: pointer;
        user-select: none;


        .month,.title {
          position: absolute;
          right: calc(100% + #{grid_sz(0,1)});
          width: grid_sz(1,0);
          text-align: right;
          @include trans; }

        .title {
          left: initial;
          top: grid_sz(0,-1);
          // right: calc(100% + #{grid_sz(1,0)})
          font-weight: 300;
          color: $color_grey_light;
          cursor: pointer;
          transform: translateY(10px); }


        li {
          min-height: 0; }

        //timelog:active 清單展開
        &.active {
          .title {
            top:  grid_sz(0,-3);
            color: $color_theme; }
          li {
            min-height: grid_sz(0,2);
            padding-bottom: grid_sz(0,1);

            .month,.title,.content {
              opacity: 1; }
            &:before {
              border: solid 0px transparent;
              @include size(11px);
              transform: translate(-6px);
              background-color: $color_theme; } } }


        &:last-child {
          li {
            border-left: none; } }


        &:first-child {}


        li {
          list-style: none;
          width: grid_sz(3,4);
          border-left: solid 1px $color_theme;
          position: relative;
          vertical-align: middle;
          // padding-bottom: grid_sz(0,1)
          padding-left: grid_sz(0.5,0);
          padding-bottom: 0;
          max-height: 0px;

          @include trans;
          .month,.title,.content {
            opacity: 0; }
          span {
            color: $color_grey_light;
            margin-top: -10px;
            @include trans; }
          span.content {
            top: -10px; }

          &:last-child {
            min-height: grid_sz(1,0); }

          &:hover {
            span {
              color: rgba(#111,0.5); } }

          text-align: left;


          //日期的球
          &:before {
            content: '';
            display: block;
            @include size(14px);
            transform: translate(-7px);
            border-radius: 50%;
            background-color: $color_theme;
            position: absolute;
            left: 0;
            top: 0;
            background-color: #FFF;
            box-sizing: border-box;
            border: solid 3px $color_theme;
            @include trans; } }


        h4+li {
            padding-top: grid_sz(0,1); } }



      .col_left {
        padding-top: grid_sz(0,7);
        padding-bottom: grid_sz(0,7);
        padding-right: grid_sz(0,2.5);
        align-items: flex-start;
        width: calc(50% + #{grid_sz(1,1.5)});
        h4 {
          font-size: 20px; }
        .section_feature {
          margin-top: grid_sz(0,3); }

        p {
          margin-top: grid_sz(0,3); }
        * {
          color: $color_white;
          font-weight: 300;
          text-align: left; } }
      .col_right {
        width: calc(50% - #{grid_sz(1,1.5)}); }

      @include rwd_md {
        display: block;
        padding: 0;
        .col_left {
          background-image: url("/img/about/1.jpg");
          background-size: cover;
          background-position: middle top; }
        .col_left,.col_right {
          margin: 0;
          padding: grid_sz(0,1);
          width: 100%;
          background-color: $color_grey_dark; }
        .col_left {
          padding-top: grid_sz(0,5);
          padding-bottom: grid_sz(0,5); }
        .col_right {
          padding: grid_sz(0,1);
          padding-top: grid_sz(0,5);
          padding-left: grid_sz(1,2);
          .timelog {
            width: 100%; } } } } }


  //中間三個格子
  .section_blocks {
    background-image: url("/img/about/2.jpg");
    background-size: cover;
    * {
      color: white;
      font-weight: 300; }
    .block {
      width: calc(100% / 3);
      padding: grid_sz(0,4) 0px;
      height: grid_sz(5,0);
      // +flex_center
      flex-direction: column;
      &:before {
        @include pseudo_ab_full;
        background-color: rgba($color_theme,0.7);
        @include trans; }
      &:nth-child(1) {
        &:before {
          background-color: rgba($color_theme,0.8); }
        &:hover {
          &:before {
            background-color: rgba($color_theme,0.85); } } }


      &:nth-child(2) {
        &:before {
          background-color: rgba( darken($color_theme,4) ,0.9); }
        &:hover {
          &:before {
            background-color: rgba($color_theme,0.95); } } }

      &:nth-child(3) {
        &:before {
          background-color: rgba($color_theme,0.85); }
        &:hover {
          &:before {
            background-color: rgba($color_theme,0.9); } } } }

    .block_title {
      font-size: 30px;
      margin-bottom: grid_sz(0,2);
      text-align: center; }
    .infos {
      width: grid_sz(4,3);
      margin: auto; }
    @include rwd_sm {
      .block {
        width: 100%;
        height: auto;
        padding: grid_sz(0,2);
        .infos {
          width: 100%;
          padding: grid_sz(0,1);
          box-sizing: border-box; } } } }

  .section_member {

    .person_area {
      display: flex;
      flex-wrap: wrap; }

    .section_title {
      margin-top: grid_sz(0,8);
      margin-bottom: grid_sz(0,5);
      text-align: center;
      font-weight: 400;
      letter-spacing: 5px;
      color: $color_grey; }

    .person_box {
      padding: $gutter_width*2 $gutter_width*1;
      position: relative;
      display: flex;
      text-align: left;
      height: grid_sz(0,15);
      margin: grid_sz(0,0.5) 0;



      .cover {
        @include full;
        background-image: url("/img/about/3.jpg"); }

      .col_left,.col_right {
        @include trans; }

      .col_left {
        width: grid_sz(2,1);
        padding: 0;
        height: 100%; }

      .col_right {
        width: grid_sz(4,3);
        padding: grid_sz(0,2) grid_sz(0,1);
        display: block;
        height: 100%;
        text-align: left; }



      &.small {
        width: grid_sz(6,5);

        height: grid_sz(5,1);
        padding: 0; }


      &.big {
        height: grid_sz(0,23);
        padding: 0;
        // margin: grid_sz(0,0.5)

        .col_left {
          width: grid_sz(4,3); }
        .col_right {
          width: grid_sz(8,7); }

        @include rwd_sm {
          .col_left,.col_right {
            width: 100%;
            padding: grid_sz(0,1);
            box-sizing: border-box; } } }

      @include rwd_sm {
        border-bottom: solid 1px rgba(0,0,0,0.1);

        &.small , &.big {
          height: auto;
          .col_left {
            height: auto;
            .cover {
              height: grid_sz(5,0); } }
          .col_left , .col_right {
            box-sizing: border-box;
            padding: grid_sz(0,1);
            width: 100%; } } }

      .bigger_text {
        font-size: 1.1em; }

      font-weight: 800;

      h5 {
        margin-top: grid_sz(0,1);
        font-size: 18px;
        &:first {
          margin-top: 0px; } }

      h5.name {
        margin-top: 0px; }

      p {
        margin-top: grid_sz(0,1);
        margin-bottom: grid_sz(0,1); }

      p,li {
        color: rgba(black,0.6); }

      ul {
        list-style: none;
        padding: 0;
        padding-top: grid_sz(0,1);
        li {
          // padding-top: 3px
          // padding-bottom: 3px
          &:before {
            content: ".";
            position: relative;
            display: inline-block;
            top: -5px;
            margin-right: 15px; } } }

      .experience {
        box-sizing: border-box;
        @include ab_full;
        opacity: 0;
        @include trans;
        padding: grid_sz(0,1);

        @include flex_center;
        align-items: flex-start;

        h5,li,p {
          color: white; }
        p {
          letter-spacing: 2px;
          font-weight: 300; } }

      &:hover {
        .col_left,.col_right {
          filter: blur(6px); }

        .experience {
          opacity: 1;
          background-color: rgba($color_theme,0.8);
          overflow-y: auto; }

        .btn_expand.cross {
          &:before,&:after {
            background-color: white; }
          &:before {
            transform: translate(-50%,-50%) rotate(45deg); }
          &:after {
            transform: translate(-50%,-50%) rotate(135deg); } }

        // p,li,h4
 } } } }        //   color: rgba(white,0.8)
