.page_index {
  opacity: 1;
  text-align: left;


  span.brown {
    color: $color_brown; }
  span.theme {
    color: $color_theme; }

  .container.flex {
    // height: 100vh
    min-height: 100vh;
    @include range_width(0px,800px) {
      height: auto; } }

  .col_left,.col_right {
    h1,h2,h3,h4,h5,h6,p {
      text-align: left; } }

  section {
    min-height: 100vh;

    .section_title {
      margin-bottom: $gutter_width*3;
      letter-spacing: 3px;
      font-size: 30px; }
    .section_feature {
      margin-bottom: grid_sz(0,1);
      padding: 0px grid_sz(0,0.5);
      letter-spacing: 3px;
      line-height: 35px;
      // width: 100%
      text-align: left; }
    .section_para {
      margin-bottom: grid_sz(0,3);
      padding: 0px grid_sz(0,0.5); }


    @include range_width(0px,800px) {
      padding: 50px 0px;

      .container {
        width: 100%;
        padding-left: 0px;
        padding-right: 0px;
        flex-direction: column;
        box-sizing: border-box;

        .col_left, .col_right {
          width: 100%;
          box-sizing: border-box;

          padding: 20px;

          .section_title , .section_para , .section_feature {
            padding-left: 0px;
            padding-right: 0px;
            box-sizing: border-box;
            width: 100%; } } } } }


  .play_btn {
    margin-bottom: grid_sz(0,1);
    border-radius: 50%;
    // background-color: $color_theme
    color: $color_white;
    @include flex_center;
    @include size(65px);
    @include trans;
    opacity: 1;
    cursor: pointer;

    &:after {
      content: "";
      display: block;
      position: absolute;
      @include ab_center;
      @include size(50px);
      background-color: $color_theme;
      border-radius: 50%;
      @include trans; }
    &:before {
      content: "";
      display: block;
      position: absolute;
      @include ab_center;
      @include size(50px);
      border: solid 1px $color_theme;
      border-radius: 50%;
      @include trans; }


    &:hover {
      &:after {
        @include size(55px); }

      &:before {
        @include size(64px); } }
    i {
      @include ab_center;
      z-index: 10;
      margin-left: 3px; } }

  .btn_video_close {
    position: absolute;
    right: 30px;
    top: 100px;
    color: white;
    font-size: 40px;
    z-index: 30;
    cursor: pointer;
    @include trans;
    &:hover {
      color: $color_theme; } }


  .page_index_info {
    min-height: 0px;
    display: flex;
    flex-direction: row;

    .container {
      text-align: center; }

    .left,.right {
      // border: solid 1px
      position: relative;
      transition: 0.8s;
      @include flex_center;
      cursor: pointer;
      height: grid_sz(0,17);
      flex-direction: column;
      box-sizing: border-box;
      width: calc(50% + #{grid_sz(5,5)});

      * {
        color: $color_white; }
      &:before {
        @include pseudo_ab_full; }

      .infos {
        padding: grid_sz(0,3) grid_sz(0,2);
        border: solid 1px white;
        margin-top: grid_sz(0,2);
        // max-width: grid_sz(3,4)

        // display: inline-block
        h2 {
          font-size: 38px;
          letter-spacing: 10px; }
        h4 {
          font-weight: 300; }

        transition: 0.8s; }
      .right .infos {
        // max-width: grid_sz(6,4)
        width: 100%; }

      p {
        margin-top: grid_sz(0,2);
        overflow: hidden;
        max-height: 0px;
        max-width: grid_sz(5,5);
        opacity: 0;
        transition: 0.8s;
        font-weight: 100; }


      &:hover {
        height: grid_sz(0,18);
        width: calc(50% + #{grid_sz(8,7)});
        .infos {
          border-color: transparent;
          padding: 0; }
        p {
          max-height: 300px;
          opacity: 1; } } }


    .left {
      background-image: url("../img/index/1b.jpg");
      // width: calc(50vw - 50% + #{grid_sz(5,5)})
      height: grid_sz(0,17);
      border-right: solid 3px white;

      .infos {
        float: right; }

      &:before {
        background-color: rgba($color_theme,0.8); } }
    .right {
      background-image: url("../img/index/1c.jpg");
      // width: calc(50vw - 50% + #{grid_sz(8,7)})
      height: grid_sz(0,17);
      border-left: solid 3px white;

      .infos {
        float: left; }
      &:before {
        background-color: rgba($color_brown,0.8); } } }


  //-----------------------------------------------
  .page_index_main {
    $news_height: grid_sz(0,12.5);
    background-image: url("../img/index/1a.jpg");
    background-size: cover;
    display: flex;
    min-height: 100vh;
    align-items: center;
    justify-content: center;
    background-position: center 0px;




    .row {
      position: relative; }
    .index_slogan_area {
      height: 100%;
      @include flex_center;
      flex-direction: column;
      .title_span {
        display: inline-block;
        width: grid_sz(1,2); } }


    @keyframes up_down {
      0% {
        transform: translate(-50%,-50%); }
      50% {
        transform: translate(-50%,-100%); }
      100% {
        transform: translate(-50%,-50%); } }


    i.fa.fa-angle-down {
      animation: up_down 2.5s infinite;
      font-size: 30px;
      margin-left: 3px;
      color: $color_theme;
      position: absolute;
      left: 50%;
      transform: translate(-50%,-50%);
      bottom: grid_sz(0,3); }

    h1 {
      letter-spacing: 20px;
      margin-bottom: 0px;
      margin-top: 0px;
      animation: lspace_in 2s 1 both;
      text-align: center;
      font-size: 48px;
      span {
        font-weight: 300;
        padding: 0px 5px; } }

    @keyframes lspace_in {
      0% {
        letter-spacing: 10px;
        opacity: 0; }
      100% {
        letter-spacing: 15px;
        opacity: 1; } }

    h3.page_header_eng {
      margin-top: 10px;
      font-size: 30px;
      letter-spacing: 2px;
      animation: lspace_in2 2s 0.5s 1 both;
      text-align: center;
      font-weight: 300;
      margin-bottom: grid_sz(0,4); }

    h6 {
      letter-spacing: 5px;
      color: $color_theme; }

    @keyframes lspace_in2 {
      0% {
        letter-spacing: 0px;
        opacity: 0; }
      100% {
        letter-spacing: 2px;
        opacity: 1; } }


    .news_container {
      position: absolute;
      bottom: 0px;
      width: grid_sz(10,9);
      height: $news_height;
      @include box_shadow; }


    .news {
      display: flex;
      flex-direction: row;
      background-color: $color_theme;
      height: 100%;

      opacity: 1;

      .col_infos,.col_img {
        height: 100%;
        position: relative; }

      .col_infos {
        width: grid_sz(6,6);
        padding: grid_sz(0,1);
        display: flex;
        flex-direction: column;
        justify-content: space-between; }

      .col_img {
        width: grid_sz(4,3); }

      .col_infos .btns {
        @include flex_split("lr");
        width: 100%;
        z-index: 10;

 }        // height: 200px
      h4,h5,p {
        color: $color_white;
        margin: 0;
        font-weight: 300; }
      h5 {
        margin-bottom: $gutter_width; }
      .cover_img {
        background-image: url("../img/homepage/Home2.jpg");
        background-position: center center;
        background-size: auto 110%;
        height: 200px+$gutter_width*3;
        margin-right: 0px;
 } }        // transition:  background-image 0.5s



    &:before {
      @include pseudo_ab_full;
      background-color: black;
      opacity: 0;
      pointer-events: none;
      @include trans; }

    &:after {
      @include pseudo_ab_full;
      color: white;
      opacity: 0;
      @include trans;
      pointer-events: none;
      z-index: 10; }
    //播放首頁的影片
    &.playmovie {
      background-color: $color_grey_dark;

      &:before {
        opacity: 1; }
      &:after {
        color: rgba(white,1);
        transition: 0.3s 0.3s;
        opacity: 1; }
      .play_btn {
        opacity: 0;
        transition: 0.3s 0.1s; }

      .index_slogan_area {

        transition: 0.7s 0.2s;
        opacity: 0; }
      .video_container {
        opacity: 1;
        transition: 0.7s 0.7s;

        pointer-events: initial; } } }

  //-----------------------------------------------

  .page_index_grow {
    // background-image: url("../img/index/2c.jpg")

    .cover {
      position: absolute;
      width: 100%;
      left: 0;
      bottom: 0; } }


  //-----------------------------------------------

  .page_index_flow {
    background-image: url("../img/index/3a.jpg");
    background-size: cover;
    @include bg_block('before','left',rgba($color_theme,0.9),grid_sz(1,2));
    overflow: hidden;

    .container.flex {
      align-items: flex-start;
      padding-top: grid_sz(3,0);
      @include rwd_sm {
        padding-top: grid_sz(0,0); } }


    &:before,.col_left {
      transition: 0.7s 0.2s;
      transform: translateX(0px); }
    .video_container {
      @include trans;
      opacity: 0;
      @include ab_full;
      @include flex_center;

      pointer-events: none;

      &:before {
        @include pseudo_ab_full;
        background-color: black;
        opacity: 0;
        pointer-events: none;
        @include trans; } }

    * {
      font-weight: 300; }
    .col_left {
      opacity: 1;
      h3,h4,p {
        color: white; } }
    .btn {
      margin-top: $gutter_width*2; }

    .percent {
      position: absolute;
      right: 0;
      left: auto;
      bottom: grid_sz(0,5); }

    .revive_box {
      background-color: $color_grey;
      padding: grid_sz(0,2) grid_sz(1,1);
      margin-left: grid_sz(0,-1);
      text-align: center;
      border-radius: 30px;
      @include trans;
      h1,h4 {
        text-align: center; }
      h1 {
        font-size: 80px;
        letter-spacing: 10px;
        margin-bottom: grid_sz(1,0);
        color: $color_brown;
        text-shadow: 12px 0px $color_theme; }
      h4 {
        font-size: 20px;
        letter-spacing: 10px;
        margin-bottom: grid_sz(0,2);
        color: $color_brown; }
      .other {
        position: absolute;
        bottom: grid_sz(0,-1.5);
        left: grid_sz(0,0.5);
        font-size: 14px;
        color: $color_grey; }
      .play_btn {
        margin-left: auto;
        margin-right: auto;
        i:before {
          color: $color_white; } } }

    &.playmovie {
      .video_container {
        &:before {
          opacity: 1; } }
      &:before {
        transform: translateX(-700px); }
      .revive_box,.col_left {
        transform: translateX(-10px);
        opacity: 0; }
      .video_container {
        opacity: 1;
        transition: 0.7s 0.7s;

        pointer-events: initial; } }

    @include rwd_sm {
      padding-bottom: 0;
      &:before {
        @include pseudo_ab_full;
        background-color: rgba($color_theme,0.9); }
      .other {
        display: none; }

      .container {
        .col_right {
          padding-bottom: 0; } } } }


  //----------------------------------------------
  .page_index_accurate {
    background-image: url("../img/homepage/Home5.jpg");
    position: relative;
    display: flex;
    justify-content: center;
    min-height: 120vh;


    .section_title,.section_para {
      color: $color_white; }

    .col_right {
      padding-top: grid_sz(0,3);
      padding-bottom: grid_sz(0,14); }

    .btn {
      margin-top: $gutter_width*2; }


    @include bg_block('before','right',$color_theme);


    .col-sm-6 {
      padding-left: 1.5*$gutter_width;
      padding-right: 1.5*$gutter_width;
      text-align: center;
      h3,p {
        color: $color_white; } }

    .btn {
      margin-bottom: 0; }

    .acc_sm_pic {
      // background-color: $color_theme
      border: none;
      width: 50%;
      height: 100%;
      cursor: pointer;

      @include range_width(0px,800px) {
        width: 100%;
        height: 100px; }

      &:hover {
        &:before {
          opacity: 1; } }
      &:before {
        @include pseudo_ab_full;
        @include ab_center;
        background-color: rgba($color_theme,0.5);
        opacity: 0;
        transition: 0.5s;
        z-index: 2; }
      &:after {
        @include pseudo_ab_full;
        @include ab_center;
        background-size: cover;
        z-index: 1; }
      h4 {
        color: $color_white;
        font-size: 20px;
        letter-spacing: 3px;
        @include ab_center;
        z-index: 3; } }

    .acc_sm_pic[data-type="1"]:after {
      background-image: url("../img/homepage/Home6-1.jpg"); }
    .acc_sm_pic[data-type="2"]:after {
      background-image: url("../img/homepage/Home6-4.jpg"); }
    .acc_sm_pic[data-type="3"]:after {
      background-image: url("../img/homepage/Home6-2.jpg"); }
    .acc_sm_pic[data-type="4"]:after {
      background-image: url("../img/homepage/Home6-3.jpg"); }


    .type_container {
      position: absolute;
      bottom: 0px;
      height: grid_sz(0,14);
      flex-direction: column;
      z-index: 100;


      .row {
        display: flex;
        @include size(grid_sz(8,8),50%);
        &:nth-child(1) {
          left: grid_sz(4,4); }
        &:nth-child(2) {
          left: grid_sz(2,2); }

        @include range_width(0px,800px) {
          flex-direction: column;
          height: grid_sz(0,28);
          width: 100%;

          &:nth-child(1) {
            left: 0; }
          &:nth-child(2) {
            left: 0; } } } } }

  //----------------------
  .page_index_team {
    height: auto;
    .container.full {
      height: grid_sz(0,18);
      min-height: 0px;
      .pic_left,.col_right {
        box-shadow: 0px 0px 20px rgba(0,0,0,0.2); }
      .col_left {
        padding: 0;
        justify-content: flex-start;
        align-items: flex-end; }
      .pic_left {
        float: right;
        width: 100%;
        max-width: grid_sz(7,6);
        background-image: url("../img/index/4a.jpg");
        height: grid_sz(0,14); }
      .col_right {
        background-image: url("../img/index/4b.jpg"); } }

    .container.content {
      padding-bottom: grid_sz(0,6);
      height: auto;
      .col_left {
        height: auto;
        padding-top: grid_sz(0,5); }
      .col_right {
        align-items: flex-start;
        .team_pic {
          @include size(100px);
          border-radius: 50%;
          margin-bottom: grid_sz(0,2);
          background-size: cover; }
        ul {
          width: 100%;
          display: flex;
          li {
            width: grid_sz(2,1);
            margin-left: grid_sz(0,0.5);
            margin-right: grid_sz(0,0.5);
            text-align: center;
            .title {
              font-size: 18px; }
            * {
              margin-left: auto;
              margin-right: auto; } } }


        h5 {
          color: $color_theme;
          text-align: center; } } }

    @include rwd_md {
      padding-top: 0;
      .container.full {
        align-items: flex-end;
        height: auto;
        .pic_left,.col_left,.pic_right,.col_right {
          height: grid_sz(0,10);
          min-height: initial; }
        .col_right {
          width: 50%;
          display: none; } }

      .container.content {
        // padding-top: grid_sz(0,0)
        padding-bottom: grid_sz(0,0);

        .col_left {
          padding-top: grid_sz(0,0); }
        .col_right {
          h5 {
            font-size: 16px; }
          ul {
            width: 100%; } } } } }

  //-------------------------
  .page_index_service {

    .container.full {
      position: absolute;
      left: 0;
      top: 0;
      .col_left {

        width: calc(50%);
        height: calc(100%);
        .out_pic {
          height: 110%;
          width: calc(100% + #{grid_sz(1,1)});
          top: 0;
          position: absolute;
          .item {
            @include size(100%,100%); } } }

      .col_right {
        width: calc(50%);
        // height: calc(100% + #{grid_sz(0,4)})
        background-color: #F6F6F6;
        .slicklogo_team {
          width: 100%; } }
      @include rwd_md {
        .col_left,.col_right {
          width: 100%; }
        .col_right {
          display: none; } } }



    .btns {
      .inline_container {
        margin: 0px grid_sz(0.5,0.5);
        display: inline-block;
        @include rwd_md {
          margin-left: 0px; } } }

    .container.flex {
      align-items: flex-start;
      justify-content: flex-start;

      .slick_caption {
        position: absolute;
        bottom: grid_sz(0,4);
        left: 0;
        display: flex;

        .title,.next {
          background-color: rgba($color_grey_dark,0.8);
          color: white;
          padding-top: grid_sz(0,1.5);
          padding-bottom: grid_sz(0,1.5);
          @include flex_center;
          border-bottom: solid 2px $color_white;
          padding: grid_sz(0,1);
          font-size: 20px;
          margin-right: 6px;
          font-weight: 300; }

        .next {
          padding-left: grid_sz(0,2);
          padding-right: grid_sz(0,2);
          text-align: center;
          cursor: pointer;
          @include trans;

          &:hover {
            background-color: $color_brown; }

          @include flex_center;
          i {
            position: relative;
            color: white;
            margin-top: 10px;
            left: 5px; } } }

      .col_left {
        padding: 0; }
      .col_right {
        height: calc(100% - #{grid_sz(0,4)});
        position: relative;
        .section_title {
          margin-top: grid_sz(0,5); }
        &:before {
          content: "";
          display: block;
          position: absolute;
          left: 0;
          top: 0;
          height: 100%;
          width: grid_sz(1,1);
          left: grid_sz(-1,-1);
          background-color: #F6F6F6; } }

      @include rwd_md {
        .slick_caption {
          bottom: 0; }
        .col_left {
          margin-bottom: grid_sz(0,-1); }
        .col_right {
          background-color: #F6F6F6;
          .section_title {
            margin-top: grid_sz(0,3); } } } }

    .pan_box {
      .section_title {
        margin-top: 0px; }
      margin-left: grid_sz(-1,-0.5);
      background-color: #F6F6F6;
      height: auto;
      padding-top: grid_sz(0,11);
      padding-bottom: grid_sz(0,7); }

    @include rwd_md {
      padding-bottom: 0; } }

  //
  .page_index_compaign {
    text-align: center;
    h3 span,h4 {
      font-weight: 300; }

    .container {
      padding: 0px grid_sz(2.5,2);
      padding-top: grid_sz(0,12);
      padding-bottom: grid_sz(0,20); }

    h1 {
      font-size: 106px;
      margin-bottom: grid_sz(1,1);
      color: #BBB288;
      letter-spacing: grid_sz(0,2);
      opacity: 0.8;

      @include rwd_md {
        font-size: 8vw;
        letter-spacing: 20px; } }
    h3 {
      font-size: 30px;
      letter-spacing: 4px;
      margin-bottom: grid_sz(0,2); }

    h4.section_para {
      max-width: grid_sz(9,9);
      // width: grid_sz(9,9)
      width: 100%;
      line-height: 40px; }
    h1,h3,h4 {
      text-align: center; }
    background-image: url("../img/index/6a.jpg");
    background-size: cover;
    text-align: center;


    @include range_width(0px,800px) {
      .container {
        padding: 12px; } } }


  //------------------------------------------------------
  .page_index_sponsor {
    min-height: initial;
    background-color: $color_grey_bg;
    .item {
      width: 25vw; }

    .container {
      height: auto;
      padding-top: grid_sz(0,3);
      padding-bottom: grid_sz(0,2);
      justify-content: space-around;
      margin: auto; }

    .sponsorlogo {
      width: grid_sz(2,2);
      margin-left: auto;
      margin-right: auto;
      text-align: center;
      // padding-left: grid_sz(0,1)
      // padding-right: grid_sz(0,1)

      mix-blend-mode: darken; } }

  @include range_width(0px, 800px) {

    section {
      // min-height: 100vh
 }      // height: 100vh

    .percent {
      font-size: 100px;
      &:after {
        font-size: 60px; } }

    .page_index_main {
      // min-height: 150vh
      background-size: cover;

      .index_slogan_area {
 }        // height: 70vh


      .news_container {
        width: 100%;
        height: auto;
        .news {
          flex-direction: column;
          height: auto;
          .col_img {
            order: -1;
            width: 100%;
            height: 200px; }
          .col_infos {
            width: 100%;
            height: auto;
            min-height: 350px; } } } }


    .page_index_grow {
      .percent {
        right: grid_sz(0,2); }


      .col_left {
        display: none; } }

    .page_index_live {
      .percent {
        bottom: 0px;
        right: grid_sz(0,2); }
      .col_right {
        min-height: initial;
        height: 0px; } }


    .page_index_accurate {
      min-height: 130vh;
      display: block;
      align-items: flex-start;

      .container.flex {
        align-items: flex-start;
        .col_left {
          display: none; }
        .col_right {
          display: block;
          padding-top: grid_sz(0,3);
          padding-bottom: grid_sz(0,3);
          .section_title {
            margin-top: 0px; } } } } } }


//----------------------------
.page_index {
  @include range_width(0px,800px) {
    h3.section_title {
      margin-top: grid_sz(1,0); }
    .col_left,.col_right {
      width: 100%; }
    .page_index_main {
      background-position: 40% center;
      .index_slogan_area {
        margin-top: 150px;
        .title_span {
          display: block;
          width: 0px; }
        h1 {
          font-size: 40px; }
        h3.page_header_eng {
          font-size: 16px; } } }

    .page_index_info {
      flex-direction: column;
      .left,.right {
        width: 100%;
        padding: 15px;
        height: auto;
        &:hover {
          width: 100%; } } } } }




