@function full_grid($c,$span,$fix: 0px) {
  @return calc( (100vw - 24px * 13) * #{$c} / 12 + 24px * #{$span} + #{$fix}); }

$color_bg_grey: #F1F3F5;
.page_news {
  .section_hero {

    .container.full {
      height: grid_sz(9,6);
      text-align: left;
      display: flex;
      flex-direction: row;
      justify-content: stretch;
      @include rwd_sm {
        height: auto;
        .col_left {
          // padding-top: grid_sz(0,9)
 }          // padding-bottom: grid_sz(0,6)
        .col_right {
          display: none;
          height: auto;
          // padding-top: grid_sz(0,2)
 } } }          // padding-bottom: grid_sz(0,2)

    .col_left,.col_right {
      padding-top: grid_sz(0,6);
      flex: 1;
      display: flex;
      // padding-bottom: grid_sz(0,7)
 }      // height: 100%
    .col_left {
      width: percent_grid(7,7);
      background-color: $color_grey_dark;
      text-align: left;
      background-image: url("/img/news1.jpg");
      background-size: cover;
      @include trans;
      &:before {
        @include pseudo_ab_full;
        background-color: rgba(#333,0.5); }
      h1.title {
        margin-top: grid_sz(0,-2);
        margin-bottom: grid_sz(0,4);
        // transform: translateY(-10px)
        letter-spacing: 6px;
        line-height: 1.5em;
        a {
          font-weight: 300; } }

      p {
        font-weight: 300;
        margin-bottom: grid_sz(0,2); }
      * {
        color: white; }
      padding-left: full_grid(1,2);
      padding-right: full_grid(1,2); }


    .col_right {
      width: percent_grid(5,6);
      vertical-align: top;
      padding-left: 0;
      padding-right: 0;

      align-items: flex-start;
      background-color: #FAFAFA;


      .date,.title,.circle {

        @include trans;

        color: $color_grey; }
      .date {
        width: grid_sz(1,0);
        text-align: right;
        font-size: 15px; }

      .circle {
        margin: 0px 14px;
        @include size(12px);
        background-color: #ccc;
        border-radius: 50%;
        @include trans; }
      .title {
        font-size: 20px;
        width: calc(100% - 84px);
        min-height: 62px;
        vertical-align: middle;
        display: flex;
        align-items: center; } }


    ul {
      text-align: left;
      padding: 0;
      width: 100%;
      li {
        padding-left: grid_sz(0,1);
        padding-top: grid_sz(0,1.5);
        padding-bottom: grid_sz(0,1.5);
        border-bottom: solid 1px rgba(black,0.05);
        &:last-child {
          border-bottom: none; }

        width: 100%;
        cursor: pointer;
        @include trans;
        &:after {
          content: "";
          display: block;
          position: absolute;
          @include size(20px);
          top: 50%;
          left: 0px;
          background-color: #FAFAFA;
          @include trans;

          transform: translate(-50%,-50%) rotate(45deg); }

        &.active,&:hover {
          background-color: $color_theme;
          &:after {
            background-color: $color_theme; }
          .title,.date {
            color: white; }
          .circle {
            background-color: white; } } } } }
  .section_news {
    .container {
      padding: grid_sz(0,4) full_grid(1,1);

      align-items: flex-start;

      @include rwd_sm {
        flex-direction: column;
        padding: 0px; } }

    .catabar {
      width: full_grid(1,1);
      min-width: grid_sz(2,2);
      font-size: 18px;
      color: $color_grey_light;
      height: 100%;
      li {
        margin-bottom: grid_sz(0,1);
        border-bottom: solid 1px transparent;
        display: inline-block;
        margin-right: 40px;
        cursor: pointer;

        &:hover {
          color: $color_theme; }

        @include trans;
        &.active,&:focus {
          padding-top: grid_sz(0,1);
          padding-bottom: grid_sz(0,1);
          border-bottom: solid 1px $color_theme;
          color: $color_theme; } }

      .active {
        font-size: 24px; }
      li:first-child {
        padding-top: grid_sz(0,1);
        // padding-bottom: grid_sz(0,1)
        &.active {
          font-size: 18px; } }
      @include rwd_sm {
        width: 100%;
        padding: grid_sz(0,1) {
          left: grid_sz(0,0.5);
          right: grid_sz(0,0.5); }

        white-space: nowrap;
        overflow-x: scroll;
        li {
          margin-bottom: 0;
          font-size: 20px;
          .active {
            padding-bottom: 0;
            font-size: 20px; } } } }


    .area_news {
      padding-left: full_grid(0,0.5);
      padding-right: full_grid(0,0.5);
      width: 100%;
      display: flex;
      flex-wrap: wrap; }
    .news_box {
      float: left;
      background-color: $color_bg_grey;
      background-image: url("/img/news2.jpg");
      margin: grid_sz(0,1);
      padding: grid_sz(0,1);
      min-height: grid_sz(0,15);
      @include flex_center;
      flex-direction: column;
      align-items: flex-start;
      box-sizing: border-box;
      // justify-content: flex-start
      width: calc( ( 100% - 3 * #{grid_sz(0,2)} ) / 3 );
      padding-bottom: grid_sz(0,1);
      margin-left: full_grid(0,0.5);
      margin-right: full_grid(0,0.5);

      text-align: left;
      * {
        color: $color_theme; }
      .title {
        @include trans;
        cursor: pointer; }
      .date,.describe {
        font-weight: 300; }
      .describe {
        overflow: hidden;
        max-height: 0px;
        margin-top: grid_sz(0,1);
        opacity: 0;
        @include trans; }
      .icon_more {
        height: 7px;
        width: 34px;
        text-align: center;
        // margin: auto
        margin-top: grid_sz(0,2);
        margin-left: auto;
        margin-right: auto;
        background: linear-gradient(90deg,$color_theme 0% ,$color_theme 20% ,transparent 20% ,transparent 40% ,$color_theme 40%,$color_theme 60%,transparent 60%,transparent 80%,$color_theme 80%,$color_theme 100%);
        opacity: 1;
        @include trans; }
      .date {
        margin-top: grid_sz(0,1);
        opacity: 0;
        @include trans; }


      i.fa.fa-search {
        position: absolute;
        right: grid_sz(0,1);
        bottom: grid_sz(0,1);
        font-size: grid_sz(0,1);
        opacity: 0;
        cursor: pointer;
        @include trans; }

      // display: inline-block
      &:before {
        @include pseudo_ab_full;
        background-color: rgba(white,0.9);
        @include trans; }

      &:hover {
        * {
          color: white; }
        .date {
          opacity: 1; }
        &:before {
          background-color: rgba(black,0.6); }
        .describe {
          max-height: 200px;
          opacity: 1; }
        i.fa.fa-search {
          opacity: 1; }
        .icon_more {
          opacity: 0;
          margin-top: -5px; } } } }

  @include rwd_sm {
    .section_hero {
      .col_left,.col_right {
        width: 100%;
        height: auto;
        padding: grid_sz(0,10) grid_sz(0,1) grid_sz(0,3) grid_sz(0,1); } }
    .section_news {
      padding: 0px;
      .cata_list>li {
        padding: grid_sz(0,1); }
      .container {
        padding: 0px;
        .top .col_right {
          padding: 0px grid_sz(0,1); } }
      .area_news {
        padding: 0px;
        width: 100%; }
      .news_box {
        width: 100%; } } } }
